.card {
   border: 1px solid #F3F3F3;
   padding: 30px;
   width: 220px;
   border-radius: 40px;
   margin-right: 30px;
   transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
   margin-bottom: 30px;
   
   .plus {
     cursor: pointer;
   }
   
   &:hover {
   transform: translateY(-5px);
   box-shadow: 0px 20px 35px rgba(0, 0, 0, 0.06);
   }
   .favorite {
     position: absolute;
     cursor: pointer;
   }
 
   span {
     font-size: 13px;
     opacity: 0.5;
     text-transform: uppercase;
   }
   b { 
     font-size: 13px;
   }
 
   h5 {
     font-weight: 400;
     font-size: 14px;
   }
 }